.header {
  background-image: url(../img/bg-header.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 30px;

  font-family: "CirceRegular";
  color: var(--light-color);
  border: 1px solid #000000;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .list {
    display: flex;
    column-gap: 20px;
    align-items: center;
    padding-top: 20px;
  }

  &__tel {
    color: var(--light-color);
    font-size: 16px;
    line-height: 24px;
  }
}

// ---------------content---------------
.header__content {
  margin-top: 77px;
  position: relative;
  padding-bottom: 175px;
}
.header__text {
  font-size: 26px;
  line-height: 38px;
}
.title {
  font-family: "CirceExtraBold";
  font-weight: 800;
  font-size: 56px;
  line-height: 120%;
}

.info__list {
  display: flex;
  column-gap: 35px;
  text-align: center;
  padding: 0;
}

.link__item {
  font-family: "CirceBold";
  font-size: 24px;
  line-height: 35px;
}

.link__text {
  font-family: "CirceRegular";
  font-size: 16px;
  line-height: 24px;
}

.tags {
  max-width: 551px;
  display: flex;
  flex-wrap: wrap;
  padding: 5px 5px;
  gap: 5px;
}

.tags__item {
  padding: 7px 10px;
  background-color: #f2f2f2;
  border-radius: 5px;
  flex: none;
  order: 2;
}

.tags__link {
  font-family: "CirceRegular";
  color: var(--light-dark);
  font-size: 14px;
  line-height: 21px;
}

.header__map {
  display: flex;
  align-items: center;
  column-gap: 55px;
  margin-top: 55px;
}
.header__btn {
  background-color: #2489ff;
  border-radius: 49px;
  display: flex;
  column-gap: 15px;
  align-items: center;
  padding-right: 31px;
  color: var(--light-color);
}

.map__img {
  padding: 3px 3px 3px;
}

.header__object {
  text-align: center;
  color: var(--light-color);
  font-family: "CirceRegular";
  font-size: 16px;
  line-height: 19px;
}

.play {
  position: absolute;
  right: 126px;
  top: 142px;
}

// ---------------content---------------
