.presentation {
  margin-top: 130px;
  margin-bottom: 175px;
  font-family: "CirceRegular";
  &__inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  &__info {
    max-width: 385px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-family: "CirceExtraBold";
    font-size: 36px;
    line-height: 120%;
    margin: 0px;
    margin-bottom: 10px;
  }
  &__text {
    max-width: 385px;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 30px;
  }

  &__btn {
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    background-color: var(--light-blue);
    border-radius: 5px;
    position: relative;
    padding: 21px 40px;
  }
  &__img {
    padding-right: 10px;
    padding-left: 40px;
  }
  .presentation__btn::after {
    content: "";
    width: 253px;
    height: 185px;
    position: absolute;
    top: -65px;
    right: -205px;
    background-image: url(../img/btn-bg.png);
  }
  &__agent {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 21px 40px;
    gap: 10px;
    background: url(../img/maks-bg.png);
    filter: drop-shadow(0px 4px 50px rgba(53, 70, 94, 0.1));
    background-repeat: no-repeat;
    max-width: 376px;
    height: 367px;
    width: 100%;
  }
  &__mixail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 12px 12px;
    background-color: var(--light-color);
    border-radius: 5px;
    font-family: "CirceBold";
    font-size: 16px;
    line-height: 24px;
    margin-top: 304px;
    margin-left: 139px;
    color: var(--light-hex);
  }
  &__span {
    font-size: 10px;
    line-height: 15px;
    color: var(--light-hex);
  }
}
